import { graphql, PageProps } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import React from 'react';
import { Helmet } from 'react-helmet';
import { ArticleHeader, LatestNewsBlock } from '../components/article';
import {
  ArticleContentContainer,
  StyledArticleContent,
} from '../components/article/styled';
import { Table } from '../components/common';
import Layout from '../components/common/Layout/Layout';
import SubscribeBlock from '../components/common/SubscribeBlock';
import TableComponent from '../components/common/Table';
import { mapArticleNodeToArticlePreviewInfo } from '../mappers';
import { MDXProvider } from '@mdx-js/react';
import { ArticleJsonLd } from 'gatsby-plugin-next-seo';
import DesktopLogo from '../images/Logo/Telgorithm/DesktopLogo.svg';
import { isBrowser } from '../utils/commonUtils';
import { GatsbySeoComponent } from '../components/common/SEO';
import CustomLink from '../components/common/Link';
import AnchorH2 from '../components/common/AnchorH2';

const components = {
  a: (props: { href: string; children: React.ReactElement }) => (
    <CustomLink address={props.href} external inline>{props.children}</CustomLink>
  ),
  table: (props: { children: React.ReactElement }) => (
    <TableComponent.Container>
      <TableComponent.Wrapper>
        <Table>{props.children}</Table>
      </TableComponent.Wrapper>
    </TableComponent.Container>
  ),
  h2: AnchorH2,
  thead: TableComponent.Header,
  tbody: TableComponent.Body,
  tr: TableComponent.Row,
  th: TableComponent.HeaderCell,
  td: TableComponent.BodyCell,
};

const ArticlePage = ({ data }: PageProps<Queries.ArticlePageQuery>) => {
  const formattedDescription = data.mdx?.excerpt

  return (
    <>
      <Helmet title={`${data.mdx?.frontmatter?.title} | Telgorithm`}>
        <meta property="og:title" content={data.mdx?.frontmatter?.title} />
        <meta property="twitter:title" content={data.mdx?.frontmatter?.title} />
        <meta name="description" content={formattedDescription}></meta>
        <meta property="og:description" content={'Blog | Telgorithm'} />
        <meta property="twitter:description" content={'Blog | Telgorithm'} />
        <meta
          property="og:image"
          content={
            data.mdx?.frontmatter?.previewImage?.childImageSharp?.gatsbyImageData.images
              .fallback?.src as string
          }
        />
        <meta
          name="twitter:image"
          content={
            data.mdx?.frontmatter?.previewImage?.childImageSharp?.gatsbyImageData.images
              .fallback?.src as string
          }
        />
      </Helmet>
      <ArticleJsonLd
        url={isBrowser ? document?.URL : ''}
        headline={data.mdx?.frontmatter?.title}
        images={[
          data.mdx?.frontmatter?.previewImage?.childImageSharp?.gatsbyImageData.images
            .fallback?.src as string,
        ]}
        datePublished={
          new Date(data?.mdx?.frontmatter?.date as string)?.toISOString() as string
        }
        authorName="Telgorithm"
        authorType="Organization"
        publisherName="Telgorithm"
        publisherLogo={DesktopLogo}
        description={formattedDescription as string}
        overrides={{
          '@type': 'BlogPosting',
        }}
      />
      <GatsbySeoComponent
        canonicalUrl={'https://www.telgorithm.com/news/' + data.mdx?.slug}
      />
      <Layout>
        <ArticleContentContainer>
          <ArticleHeader
            articleType={data.mdx?.frontmatter?.articleType as string}
            date={data.mdx?.frontmatter?.date as string}
          ></ArticleHeader>
          <StyledArticleContent>
            <MDXProvider components={components}>
              <MDXRenderer children={data?.mdx?.body ?? 'No content'} />
            </MDXProvider>
          </StyledArticleContent>
          <LatestNewsBlock
            latestNews={data.allMdx.edges.map((edge) =>
              mapArticleNodeToArticlePreviewInfo(edge.node),
            )}
          ></LatestNewsBlock>
          <SubscribeBlock
            title={'Subscribe to our newsletter.'}
            description={
              'Navigate the cloud communications world with knowledge. Subscribe to our blog newsletter to receive the latest industry news, tips, and guidance.'
            }
          />
        </ArticleContentContainer>
      </Layout>
    </>
  );
};

export default ArticlePage;

export const query = graphql`
  query ArticlePage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        articleType
        previewImage {
          childImageSharp {
            gatsbyImageData(quality: 40)
          }
        }
      }
      body
      excerpt(pruneLength: 300)
      slug
    }
    allMdx(
      filter: { fileAbsolutePath: { regex: "//news//" } }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            articleType
            date(formatString: "MMMM DD, YYYY")
            title
            previewImage {
              childrenImageSharp {
                gatsbyImageData(layout: FIXED)
              }
            }
          }
          id
          slug
        }
      }
    }
  }
`;
